import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class DataService {

  public _url = 'https://murmuring-brook-15036.herokuapp.com';

  constructor(private http: HttpClient) { }

  sendTweet(tweet) {
    return this.http.post(this._url + '/sendtweet', {
      'tweetbody': tweet
    })
  }
}
